export default {
	data() {
		return {
			flag: true,
			user_machine: '',
			machine_id: '',
			user_agreement: '',
			shade: false,
            sha:false,
            ress:'',
            verify:false
		};
	},
	onShow() {
		uni.request({
			url: this.url + 'usermachine/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success: res => {
				console.log(res);
				this.user_machine = res.data.data.machine_datas[1];
                console.log(this.user_machine)
				if (this.user_machine.length == 0) {
					this.flag = false;
				}
				//else {
				// 	this.flag = false
				// }
				this.user_agreement = res.data.data.user_agreement;
				if (this.user_agreement == 0) {
					this.shade = true;
				} else if (this.user_agreement == 1) {
					this.shade = false;
				}
                this.ress = res.data.data.machine_datas[0]
                if(this.ress == 0){
                    this.verify = true
                }else{
                    this.verify = false
                }
                console.log(this.ress);
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		sure: function() {
			uni.navigateTo({
				url: '../../pages/agreement/agreement'
			});
		},
		cancel: function() {
			uni.navigateBack({
				url: '../../pages/my/my'
			});
		},
		onNavigationBarButtonTap: function() {
			uni.navigateTo({
				url: '../../pages/agreement/agreement'
			});
		},

		select: function(item) {
			uni.navigateTo({
				url: '../machine-detail/machine-detail?machine_id=' + item.machine_id
			});
		},
        prompt:function() {
            this.sha = true
        },
        can:function () {
            this.sha = false
        },
        validation:function () {
            uni.request({
                url:this.url + 'usermachine/affirm/',
                method:'POST',
                header:{
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    console.log(res)
                }
            })
            uni.showToast({
                title:'已验证'
            })
            this.sha = false
            this.verify = false
            
        }
	}
};
